<template>
  <div>
    <div class="flash-sale" v-if="activityType === 0 && flashSaleList.length">
      <div class="bg-tag"/>
      <div class="title">
        <span class="text">秒杀专区</span>
      </div>
      <div class="horionzal-content">
        <div class="horionzal-row">
          <div class="time-item" v-for="(item, index) in flashSaleList" :key="index" @click="changeTab(index)">
            <div class="time-text" :class="{active: currentTab === index}">
              <img v-if="item.dayStr==='往日'" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/previous-sale3x.png" class="time-img" alt="">
              <span v-else>{{item.timeStr}}</span>
            </div>
            <div class="time-status" :class="{active: currentTab === index}"><span>{{item.timeTip}}</span></div>
          </div>
        </div>
      </div>
      <product-item
        v-for="(item) in activeProductList.slice(0,showSize)"
        :key="item.sku"
        :activityType="0"
        :productInfo="item"
        :storeId="storeId"
        @onTimeDown="updateData(activityType)"
      />
      <div
        class="flex-row bottom-tag"
        style="justify-content: center;"
        v-if="activeProductList.length > 2"
        @click="changeExpand()"
      >
        {{ isExpand ? '收起' : '查看全部'}}<img class="arrow" :src="arrowUrl"/>
      </div>
    </div>
    <div class="flash-sale" v-if="activityType !== 0 && prodList.length">
      <div class="bg-tag"/>
      <div class="title" style="padding-bottom: 0;">
        <span class="text" v-if="activityType === 1">预售专区</span>
        <span class="text" v-if="activityType === 2">发现好货</span>
        <span class="text" v-if="activityType === 3">拼团专区</span>
      </div>
      <product-item
        v-for="(item) in prodList.slice(0, showSize)"
        :key="item.sku"
        :activityType="activityType"
        :productInfo="item"
        :storeId="storeId"
        @onTimeDown="updateData(activityType)"
      />
      <div
        class="flex-row bottom-tag"
        style="justify-content: center;"
        v-if="prodList.length > 2"
        @click="changeExpand()"
      >
        {{ isExpand ? '收起' : '查看全部'}}<img class="arrow" :src="arrowUrl"/>
      </div>
    </div>
  </div>
</template>
<script>
import productItem from './barActivityProdItem';
const ARROW_DOWN = 'http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_arrow_down.png';
const ARROW_UP = 'http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_arrow_up.png';
export default {
  props: {
    activityType: { // 活动类型 0-秒杀专区 1-预售专区 2-发现好货
      type: Number,
      default: 1,
    },
    flashSaleList: {
      type: Array,
      default: () => [],
    },
    prodList: {
      type: Array,
      default: () => [],
    },
    storeId: {
      type: String,
      default: '',
    },
  },
  components: {
    productItem,
  },
  data() {
    return {
      currentTab: 0,
      arrowUrl: ARROW_DOWN,
      isExpand: false,
      showSize: 2, // 展示条数
    };
  },
  computed: {
    activeProductList () {
      return this.flashSaleList.length > this.currentTab ? this.flashSaleList[this.currentTab].products : [];
    },
  },
  methods: {
    changeTab(index) {
      this.currentTab = index;
    },
    changeExpand() {
      this.isExpand = !this.isExpand;
      if (this.activityType === 0) {
        this.showSize = this.isExpand ? this.activeProductList.length : 2;
      } else {
        this.showSize = this.isExpand ? this.prodList.length : 2;
      }
      this.arrowUrl = this.isExpand ? ARROW_UP : ARROW_DOWN;
    },
    updateData() {
      this.$emit('updateData', this.activityType);
    },
    goProduct(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${this.storeId || ''}`);
    },
  },
}
</script>
