/**
 * 拼团相关
 */
import Url from '../url.js';
// import HOST from '@/config';
import { get } from '../http'

/**
  * 首页拼团列表
  */
export const getMainGroupBooking = params => get(Url.getMainGroupBooking, params);
/**
  * 店铺首页拼团列表
  */
export const getShopGroupList = params => get(Url.getShopGroupList, params);
/**
  * 单品页待成团列表
  */
export const getGoodsGroupList = params => get(Url.getGoodsGroupList, params);
/**
  * 根据订单号查询拼团信息
  */
export const getGroupBuyByOrderSn = params => get(Url.getGroupBuyByOrderSn, params);
/**
  * 根据orderId查询orderSn
  */
export const isGroupBuyOrder = params => get(Url.isGroupBuyOrder, params);

export default {
  getMainGroupBooking,
  getShopGroupList,
  getGoodsGroupList,
  getGroupBuyByOrderSn,
  isGroupBuyOrder
};
