<template>
  <div>
    <div class="flex-row" style="padding: .3rem 0;" v-if="activityType === 0" @click="goProduct()">
      <img class="product-img" :src="productInfo.imageUrl | imageToHttps | imageToWebp($webpSurpport)"/>
      <div class="flex-column" style="position: relative;">
        <div class="product-name">
          {{productInfo.productName}}
        </div>
        <div class="sale-point">
          {{productInfo.sellPoint ? productInfo.sellPoint.join(' | ') : ''}}
        </div>
        <div class="flash-sale-price">
          ¥<span>{{productInfo.flashsalePrice | formatPrice | formatPriceInt}}</span>{{productInfo.flashsalePrice | formatPrice | formatPriceFloat}}
          <p class="flash-sale-origin-price">¥{{productInfo.miniPrice | formatPrice}}</p>
        </div>
        <div class="time-down" v-if="timeDown > 0">仅剩<count-down :time="timeDown" @finish="onTimeDown"/></div>
        <div class="absolute-row pre-btn">
          立即抢购
        </div>
      </div>
    </div>
    <div class="flex-row" style="padding: .3rem 0;" v-else @click="goProduct()">
      <img class="product-img" :src="productInfo.imageUrl || productInfo.imageId || productInfo.picture"/>
      <div class="flex-column" style="position: relative; with: 4.62rem">
        <div class="product-name">
          {{productInfo.productName}}
        </div>
        <div class="sale-point">
          {{productInfo.sellPoints ? productInfo.sellPoints.replace(/,/g, ' | ') : ''}}
        </div>
        <div class="pre-sale-origin-price" :style="{color: activityType === 3 ? 'white' : '#999'}">
          原价<span class="price">¥<span class="number">{{(productInfo.activeLowerPrice || productInfo.marketPrice) | formatPrice}}</span></span>
          {{productInfo.showActiveTag ? '起' : ''}}
        </div>
        <div class="flex-row" style="align-items: baseline">
          <div class="pre-sale-price">
            ¥<span>{{productInfo.advance | formatPrice | formatPriceInt}}</span><p class="symbol">{{productInfo.advance | formatPrice | formatPriceFloat}}{{activityType === 1  ? '订金' : ''}}{{productInfo.showActiveTag ? '  起' : ''}}  </p>
          </div>
          <div class="flex-row" style="align-items: center;" v-if="activityType === 1 && !!productInfo.advance && !!productInfo.deduct">
            <span class="pre-tag"><span>预</span></span>
            <div class="price-discount">
              <span>{{productInfo.advance || 0}}抵{{productInfo.deduct || ''}}</span>
            </div>
          </div>
          <div class="book-num" v-else-if="activityType === 3">
            <span>{{productInfo.clustersNum}}人团</span>
          </div>
        </div>
        <div class="absolute-row">
          <div class="time-down" style="margin-left: 0;" v-if="timeDown > 0">
            仅剩<count-down :time="timeDown" @finish="onTimeDown"/>
          </div>
          <div class="pre-btn" v-if="activityType === 1">立即预定</div>
          <div class="pre-btn" v-if="activityType === 2">
            <span>立即抢购</span>
            <span>已售{{productInfo.saleNum}}件</span>
          </div>
          <div class="pre-btn" v-if="activityType === 3">立即抢购</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { CountDown } from 'vant';
/** 活动tab  秒杀专区 预售专区商品item */
export default {
  props: {
    activityType: { // 活动类型 0-秒杀专区 1-预售专区 2-发现好货
      type: Number,
      default: 0,
    },
    productInfo: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: ''
    },
  },
  components: {
    CountDown,
  },
  computed: {
    timeDown() {
      const {
        startTime,
        endTime,
        systemTime,
      } = this.productInfo;
      let time = 0;
      if (startTime > systemTime) {
        time = startTime - systemTime;
      } else if (endTime > systemTime) {
        time = endTime - systemTime;
      }
      console.log('倒计时：time:', time * (this.activityType ? 1000 : 1));
      return time * (this.activityType ? 1000 : 1);
    }
  },
  methods: {
    goProduct() {
      this.$router.push(`/goodsDetail?productId=${this.productInfo.productId}&visitStore=${this.storeId}&skku=${this.productInfo.skku || ''}`);
    },
    onTimeDown() {
      this.$emit('onTimeDown', this.activityType);
    }
  },
}
</script>
