<template>
  <div class="bar-activity">
    <!-- 秒杀专区 -->
    <activity-component
      :activityType="0"
      :storeId="storeId"
      :flashSaleList="flashSaleList"
      @updateData="updateData(0)"
    />
    <!-- 预售专区 -->
    <activity-component
      :activityType="1"
      :storeId="storeId"
      :prodList="preSaleProdList"
      @updateData="updateData(1)"
    />
    <!-- 发现好货 -->
    <activity-component
      :activityType="2"
      :storeId="storeId"
      :prodList="findGoodsProdList"
      @updateData="updateData(2)"
    />
    <!-- 拼团专区 -->
    <activity-component
      :activityType="3"
      :storeId="storeId"
      :prodList="groupBookingList"
      @updateData="updateData(3)"
    />
    <common-empty v-if="!isFetching && flashSaleList.length === 0 && preSaleProdList.length === 0 && findGoodsProdList.length === 0 && groupBookingList.length === 0"/>
  </div>
</template>
<script>
import {
  getStoreFlashSale,
  getPreSales,
  getFindGoods
} from '@/http/service/shop';
import {
  getShopGroupList
} from '@/http/service/groupBooking'
import { mapGetters } from 'vuex';
import commonEmpty from '../commonEmpty';
import activityComponent from './barActivityComponent';
/** 千店千面 头部tabbar 【活动】 */
export default {
  props: {
    storeId: {
      type: String,
      default: '',
    },
    storeSign: {
      type: Object,
      default: () => {},
    },
  },
  components: {
    activityComponent,
    commonEmpty,
  },
  data() {
    return {
      isFetching: true,
      currentTab: 0,
      flashSaleList: [], // 秒杀专区列表
      preSaleProdList: [], // 预售专区列表
      findGoodsProdList: [], // 预售专区列表
      groupBookingList: [], // 拼团专区列表
    };
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
    // activeProductList () {
    //   return this.flashSaleList.length > this.currentTab ? this.flashSaleList[this.currentTab].products : [];
    // },
  },
  watch: {
    watch: {
      location: {
        handler(newVal) {
          this.getFlashSale();
        },
        immediate: true,
      },
    },
    mId() {
      this.getFlashSale();
      this.getPreSale();
      this.getFindGoods();
      this.getGroupBooking();
    }
  },
  mounted() {
    console.log('活动 mounted:', this.storeId);
    this.getFlashSale();
    this.getPreSale();
    this.getFindGoods();
    this.getGroupBooking();
  },
  methods: {
    updateData(activityType) { // 0-秒杀专区 1-预售转圈 2-发现好货
      switch (activityType) {
        case 0:
          this.getFlashSale();
          break;
        case 1:
          this.getPreSale();
          break;
        case 2:
          this.getFindGoods();
          break;
        case 3:
          this.getGroupBooking();
          break;
      }
    },
    getFlashSale() {
      getStoreFlashSale({
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        streetId: this.location.townshipId,
        storeId: this.storeId
      }).then(res => {
        if (res.success) {
          const _result = res.data
          if (Array.isArray(_result.dates) && _result.dates.length) {
            let _list = []
            _result.dates.forEach(v => {
              _list.push(...v.list)
            })
            this.saleData = {
              ..._result.dates[0],
              list: _list,
              systemTime: _result.systemTime
            }
            this.handleSaleData(this.saleData);
          }
        } else {
          this.$toast(res.message)
        }
        this.isFetching = false
      })
    },
    handleSaleData(data) {
      if (!data.list || !data.list.length) {
        return
      }
      const { systemTime, list: _list } = data;

      let previousSale = {
        dayStr: '往日',
        timeTip: '进行中',
        promotionState: 1,
        isSaling: true,
        products: []
      }
      let commonList = []
      for (const item of _list) {
        if (item.dayStr === '往日') {
          if (item.products.length) {
            previousSale.products.push(...item.products)
          }
        } else {
          if (systemTime > item.endTime) {
            // 已结束
            item.promotionState = 2
            item.isSaling = false
          } else if (systemTime < item.startTime) {
            // 即将开始
            item.promotionState = 0
            item.isSaling = false
          } else {
            // 正在疯抢
            item.promotionState = 1
            item.isSaling = true
          }
          commonList.push(item)
        }
      }
      if (previousSale.products.length) {
        commonList = [previousSale, ...commonList]
      }

      let index = 0;
      for (var i = 0; i < commonList.length; i++) {
        let item = commonList[i];
        if (new Date(item.startTime).getDate() === new Date(systemTime).getDate()) {
          if (systemTime < item.endTime && systemTime > item.startTime) {
            index = i;
            break;
          }
        }
      }
      this.currentTab = index;
      this.flashSaleList = commonList
    },
    getPreSale() {
      getPreSales({
        storeId: this.storeId,
        pageIndex: 1,
        pageSize: 999999,
      }, [4, 5, 6].includes(this.storeSign.storeType)).then(res => {
        if (res.success) {
          this.preSaleProdList = res.data || [];
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
        this.isFetching = false
      });
    },
    getFindGoods() {
      getFindGoods({
        storeId: this.storeId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        streetId: this.location.townshipId,
        from: 1,
      }).then(res => {
        if (res.success) {
          !!res.data && res.data.map(item => {
            this.findGoodsProdList.push({
              ...item,
              marketPrice: item.crossedPrice,
              advance: item.price,
              showActiveTag: !!item.minPrice, // 多规格活动价【起】标签显示
            });
          })
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    },
    getGroupBooking() {
      getShopGroupList({ storeId: this.storeId }).then(res => {
        if (res.success) {
          !!res.data && res.data.map(item => {
            this.groupBookingList.push({
              ...item,
              systemTime: item.sysTime ? (item.sysTime / 1000) : 0,
              startTime: item.startTime ? (item.startTime / 1000) : 0,
              endTime: item.endTime ? (item.endTime / 1000) : 0,
              marketPrice: item.basePrice,
              advance: item.activityPrice,
              sellPoints: !item.sellPoints || item.sellPoints === 'null' ? '' : item.sellPoints,
              showActiveTag: false, // 多规格活动价【起】标签显示
            });
          })
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      })
    }
  },
}
</script>
<style lang="scss" scoped>
.bar-activity {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2.24rem;
}
.bg-tag {
  position: absolute;
  top: .24rem;
  left: .16rem;
  height: .4rem;
  width: .4rem;
  border-radius: .2rem;
  background: linear-gradient(270deg,#f8faff 27%, #d6e5ff 87%);
  z-index: 0;
}
</style>
